<template>
	<div class="box">
		<Header title="会议室发布" />
		<div v-if="general" class="back" @click="handBack">
			<div class="alert" @click="cancelBubble">
				<p class="tip">LED屏设置</p>
				<p class="tip_1">屏幕大小<input type="text">×<input type="text">米</p>
				<p class="tip_1">价格<input type="text"> 元</p>
				<p class="choice">
					<span @click="backHide">取消</span>
					<span>确定</span>
				</p>
			</div>
		</div>
		<div class="body">
			<ul>
				<li class="item">
					<span class="name">会议室原价:</span>
					<span class="hide_1"><input type="text" placeholder="请填写会议室原价"></span>
				</li>
				<li class="item">
					<span class="name">会议室价格:</span>
					<span class="hide_1"><input type="text" placeholder="请填写会议室价格"></span>
				</li>
				<li class="item">
					<span class="name">会议室数量:</span>
					<span class="hide_1"><input type="text" placeholder="请填写酒店会议室数量"></span>
				</li>
				<li class="item">
					<span class="name">会议室面积:</span>
					<div class="select_1">
						<el-select v-model="select1" slot="prepend" placeholder="请选择">
					      <el-option label="60平方" value="1"></el-option>
					      <el-option label="70平方" value="2"></el-option>
					      <el-option label="80平方" value="3"></el-option>
					    </el-select>
					</div>
				</li>
				<li class="item">
					<span class="name">窗户:</span>
					<div class="select_1">
						<el-select focus="this.blur()" v-model="select6" slot="prepend" placeholder="请选择">
					      <el-option label="是" value="1"></el-option>
					      <el-option label="否" value="2"></el-option>
					      <el-option label="部分有窗" value="3"></el-option>
					    </el-select>
					</div>
				</li>
				<li class="item">
					<span class="name">提供LED屏:</span>
					<span class="check_box"><el-checkbox v-model="checked1">&nbsp;</el-checkbox></span>
				</li>
				<li class="item" v-if="checked1">
					<span class="right">
						<el-checkbox-group v-model="checkList2">
							 <el-checkbox label="1 × 2米LED屏"></el-checkbox><br/>
	                         <el-checkbox label="1.5 × 3.5米LED屏"></el-checkbox><br/>
	                         <el-checkbox label="2.5 × 5.5米LED屏"></el-checkbox>
	                    </el-checkbox-group>
                         <div class="led_money">
                         	<p>299元</p>
                         	<p>499元</p>
                         	<p>599元</p>
                         </div>
                         <span @click="handGeneral">
                            <span class="i">
                            <i class="fas fa-plus-square"></i><span class="text">添加</span>
                            </span>
                         </span>
					</span>
				</li>
				<!-- <li class="item">
					<span class="name">服务手机号:</span>
					<span class="hide_1"><input type="text" placeholder="请输入会议室服务手机号"></span>
				</li> -->
				<li class="item">
					<span class="name">可容纳人数:</span>
					<span class="hide_1"><input type="text" placeholder="请输入会议室大约可容纳人数"></span>
				</li>
				<li class="item_1">
				    <span class="name">房间照片:</span>
				    <div class="photo">
						<div class="icon">
							<label for="file"><i class="far fa-plus-square"></i></label>
							<input type="file" id="file" style="display: none">
						</div>
						<span class="hide_1">最多添加4张</span>
					</div>
				</li>
				<li class="item ">
				    <p class="padding-bottom">房间配套设施:</p>
					<div class="check">
						<div>
							  <el-checkbox-group v-model="checkList">
							  	<span>
							  	   <el-checkbox label="话筒"></el-checkbox>
							  	</span>
							  	<span>
							  	    <el-checkbox label="有线无线宽带"></el-checkbox>
							  	</span>
							  	
							  </el-checkbox-group><br/>
						</div>
						<div >
							  <span class="add_more"><i class="far fa-plus-square"></i></span>
							  <span class="=addmore_text">添加更多</span>
						</div>
					</div>
				</li>
			</ul>
			<div class="add_room">
				<span><i class="far fa-plus-square"></i></span>
				<span class="color">添加会议室</span>
			</div>
			<div class="send">
				<button class="green_btn">发布</button>
			</div>
		</div>
	</div>
</template>
<script>
	export default {
		data(){
			return {
				  input3: '',
			      input4: '',
			      input5: '',
			      select1: '60平方',
			      select6: '是',
			      checkList: ['话筒'],
			      checked: true,
			      checked1: false,
			      radio: '1',
			      general: false,
			      checkList2: ['1 × 2米LED屏']
			}
		},
		methods: {
			handGeneral(event){
					this.general = true
				},
			handBack(){
				this.general = false
			},
			cancelBubble(event){
				event.cancelBubble = true
			},
			backHide(){
				this.general = false
			}
		}
	}
</script>
<style scoped lang="scss">
	@import "../../../common/css/common.scss";
	.box{
		width: 100%;
		font-size: rem(13px);
		.back{
			background-color: rgba(0,0,0,0.3);
			position: fixed;
			top: 0;
			left: 0;
			height: 100%;
			z-index: 999;
			width: 100%;
			font-size: rem(14px);
			.alert{
				width: 80%;
				background-color: #ffffff;
				position: absolute;
				top: rem(200px);
				left: 10%;
				text-align: center;
				border-radius: rem(8px);
				.choice{
					span{
						&:last-child{
							color: #43c122;
						}
					}
				}
				.tip{
					font-weight: bold;
				}
				.tip_1{
					width: 58%;
					margin: 0 auto;
					input{
						width: 20%;
						text-align: center;
						border-top: none;
						border-left: none;
						border-right: none; 
						border-bottom: #333 solid rem(1px);
						margin: 0;
					}
				}
				p{
					padding-top: rem(20px);
					display: flex;
					justify-content: space-around;
					&:first-child{
						font-weight: bold;
					}
					span{
						border-top: #aaa solid rem(1px);
						width: 50%;
						padding: rem(10px) 0;
						&:first-child{
							border-right: #aaa solid rem(1px);
						}
					}
				}
			}
		}
		.body{
			padding: 0 rem(16px);
			.item{
				position: relative;
				border-bottom: #e5e5e5 solid rem(1px);
				padding: rem(15px) 0;
				.right{
					position: relative;
					display: inline-block;
					width: 98%;
					.led_money{
						font-size: rem(13px);
						position: absolute;
						top: 0;
						right: 0;
					}
					.i{
						font-size: rem(14px);
						color: #409EFF;
						margin-top: rem(20px);
						display: inline-block;
						.text{
							font-size: rem(15px);
							margin-left: rem(7px);
						}
					}
				}
				.check{
					position: relative;
					padding-bottom: rem(5px);
					div{
						&:first-child{
							span{
								margin-right: 5%;
							}
						}
					}
					// .check_box{
					// 	padding: rem(3px) 5%;
					// }
					// .add_box{
					// 	padding: rem(10px) 0;
					// 	position: absolute;
					// 	bottom: rem(-20px);
					// 	right: 0;

					// }
				}
				.padding-bottom{
					padding-bottom: rem(10px);
				}
				.name{
					display: inline-block;
					width: 22%;
				}
				.check_box{
					float: right;
				}
				.select{
					margin-left: 5%;
			     }
				.hide{
					margin-left: 2%;
					color: #e5e5e5;
				}
				.hide_1{
					color: #e5e5e5;
					input{
						width: 60%;
						border: none;
						&::-webkit-input-placeholder { 
						    color:    #aaa;
						}
						&:-moz-placeholder { 
						    color:    #aaa;
						}
						&::-moz-placeholder { 
						    color:    #aaa;
						}
						&:-ms-input-placeholder { 
						    color:    #aaa;
						}
					}
				}
				.select_1{
					display: inline-block;
				}
				.add_more{
					color: #43c122;
					font-size: rem(14px);
				}
			}
			.room{
				margin-top: rem(19px);
				margin-bottom: rem(35px);
			}
			.item_1{
				border-bottom: #e5e5e5 solid rem(1px);
				padding: rem(15px) 0;
				.photo{
				    padding-bottom: rem(4px);
				.icon{
					display: inline-block;
					border: #c9c9c9 solid rem(1px);
					padding: rem(32px) rem(35px);
					margin-left: 35%;
					border-radius: rem(9px);
					color: #43c122;
				 }
				.hide_1{
					color: #aaa;
					margin-left: 5%;
				}
			 }
			}
			.add_room{
				text-align: center;
				padding: rem(20px) 0;
				color: #43c122;
				span{
					&:first-child{
						font-size: rem(14px);
					}
					&.color{
						color: #000;
					}
				}
				
			}
		}

		.send{
			padding: rem(10px) rem(20px);
		}
	}
</style>


